import { NgModule } from '@angular/core';
import { HttpModule } from "@angular/http";
import { FormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';

/* import modules */
import { OnBoardComponent } from './onboard.component';
import { OnBoardHomeComponent } from './onboard.home.component';
import { OnBoardListComponent } from './onboard.list.component';
import { TabsModule } from './tab/tab.module';
import { HeaderModule } from './header/header.module';
import { RequirementModule } from './requirements/requirement.module';
import { PnpModule } from './pnp/pnp.module';
import { SubmitforapprovalModule } from './submitforapproval/submitforapproval.module';
import { AccessPermission } from '../guards/access-permission';

import { routesOnboard } from './onboard.routing';

@NgModule({
	declarations: [
		OnBoardComponent, OnBoardHomeComponent, OnBoardListComponent, AccessPermission
	],
	imports: [
		FormsModule,
		HttpModule,
		TabsModule,
		HeaderModule,
		RequirementModule,
		PnpModule,
		SubmitforapprovalModule,
		RouterModule.forChild(routesOnboard)
	],
	exports: [
		OnBoardComponent, OnBoardHomeComponent, OnBoardListComponent, AccessPermission
	],
	providers: []
})
export class OnBoardModule {

}



