import { Directive, ElementRef, OnInit } from '@angular/core';
import { LoginService } from '../login/login.service';

@Directive({
  selector: '[access-permission]' //this is how you address an attribute directive
})
export class AccessPermission implements OnInit {
  userRole:any = undefined;
  constructor(private loginService: LoginService, private _elementRef: ElementRef) { }

  ngOnInit() {
    this.checkPermission();
  }

  private checkPermission(): void {
    let el: HTMLElement = this._elementRef.nativeElement;
    let hasAccess = false;
    let allowedAccess = el.getAttribute("access-permission").split(" ");
    for (let i = 0; i < allowedAccess.length; i++) {
      if (this.userHasRole(allowedAccess[i])) {
        hasAccess = true;
        break;
      }
    }

    if (!hasAccess) {
      el.parentNode.removeChild(el);
    }
    
  }

  private userHasRole(role) {
    if (!this.userRole) {
      let currentUser = this.loginService.getCurrentUser();
      this.userRole = currentUser ? currentUser.permissions : [];
    }
    for (var j = 0; j < this.userRole.length; j++) {
      if (role == this.userRole[j]) {
        return true;
      }
    }
    return false;
  }
}


