import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { environment } from 'environments/environment';
import { Router } from '@angular/router';
import { Constants } from '../../common/constants';
import { CookieService } from 'angular2-cookie/services/cookies.service';
import {Login } from '../../login/login';


@Injectable()
export class TokenService {
    currentUser: Login;
    constructor(private router: Router, private cookieService: CookieService) { 
    }

    loadToken(): any {
        return localStorage.getItem(environment.clientCredentialTokenName);
    }

    saveToken(access_token: string) {
        this.clearToken();
        localStorage.setItem(environment.clientCredentialTokenName, access_token);
        return Observable.of(environment.clientCredentialTokenName);
    }

    clearToken() {
        localStorage.removeItem(environment.clientCredentialTokenName);
        return Observable.of(true);
    }

    saveCookie(cookie_value: Object) {
        let cookieOption = { path: "/", domain: "." + Constants.getDomainName(), expires: this.addDays(environment.cookieExpireDays) };
        this.cookieService.putObject(environment.clientCredentialCookieName, cookie_value, cookieOption);
    }

    getCookie() {
        this.currentUser = this.cookieService.getObject(environment.clientCredentialCookieName) as Login;
        return (this.currentUser.token || "");
    }

    clearCookie() {
        this.cookieService.remove(environment.clientCredentialCookieName);
    }
    addDays(days) {
        let dt = new Date();
        dt.setDate(dt.getDate() + parseInt(days));
        return dt;
    }
}


