import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';

import { OnBoard } from './onboard';
import { environment } from 'environments/environment';

import { HttpHelperService } from '../services/httphelper/httphelper.service';

@Injectable()

export class OnBoardService {
    errorMessage: string;

    constructor(private http: Http, private httpHelperService: HttpHelperService) {

    }

    dashboard(): Observable<String> {
        return this.http.get(environment.dashboardApi, this.httpHelperService.getAuthRequestOptionsArg())
            //return this.http.get("../app/Dashboard/Dashboarddata.json", this.httpHelperService.getAuthRequestOptionsArg())
            .map(this.extractDashboardData)
            .catch(this.handleDashboardError);
    }

    private extractDashboardData(res: Response): String {
        return res.toString();
    }

    private handleError(error: any): Observable<String[]> {
        // in a real world app, we might use a remote logging infrastructure
        // we'd also dig deeper into the error to get a better message
        let errMsg: any = (error.message) ? error.message :
            error.status ? `${error.status} - ${error.statusText}` : 'Server error';
        console.error(errMsg); // log to console instead
        return Observable.throw(errMsg);
    }

    private handleDashboardError(error: any): Observable<String> {
        // in a real world app, we might use a remote logging infrastructure
        // we'd also dig deeper into the error to get a better message
        let errMsg: any = (error.message) ? error.message :
            error.status ? `${error.status} - ${error.statusText}` : 'Server error';
        console.error(errMsg); // log to console instead
        return Observable.throw(errMsg);
    }

}


