import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';

import { Login } from './login';
import { environment } from 'environments/environment';

import { HttpHelperService } from '../services/httphelper/httphelper.service';

@Injectable()

export class LoginService {
    currentUser: Login;
    errorMessage: string;

    constructor(private http: Http, private httpHelperService: HttpHelperService) {

    }

    getCurrentUser() {
        return localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {};
    }

    login(login: Login): Observable<Login>{
        //return this.http.get(environment.loginApi + '/' + login.username + '/' + login.password, this.httpHelperService.getAuthRequestOptionsArg())
        return this.http.get("../app/login/logindata.json", this.httpHelperService.getAuthRequestOptionsArg())
            .map(this.extractLoginData)
            .catch(this.handleLoginError);
    }

    private extractLoginData(res: Response): Login {
        let body: Login = res.json();

        if (body) {
            localStorage.setItem("user", JSON.stringify(body));
            return body;
            // let login = new Login();
            // login.username = body.username;
            // login.firstName = body.firstName;
            // login.lastName = body.lastName;
            // login.password = body.password;
            // return login;
        } else {
            throw new Error("Invalid username and password.");
            //return new Login();
        }
    }

    private handleError(error: any): Observable<Login[]> {
        // in a real world app, we might use a remote logging infrastructure
        // we'd also dig deeper into the error to get a better message
        let errMsg: any = (error.message) ? error.message :
            error.status ? `${error.status} - ${error.statusText}` : 'Server error';
        console.error(errMsg); // log to console instead
        return Observable.throw(errMsg);
    }

    private handleLoginError(error: any): Observable<Login> {
        // in a real world app, we might use a remote logging infrastructure
        // we'd also dig deeper into the error to get a better message
        let errMsg: any = (error.message) ? error.message :
            error.status ? `${error.status} - ${error.statusText}` : 'Server error';
        console.error(errMsg); // log to console instead
        return Observable.throw(errMsg);
    }

}


