import { Component } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router'

import { Login } from './login';
import { LoginService } from './login.service';
import { TokenService } from '../services/token/token.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent {
	login = new Login();
	errorMessage: string;
	
	constructor(private route: ActivatedRoute,
		private router: Router, private loginService: LoginService, private tokenService: TokenService) {
	}

  ngOnInit() {
	  // Get the modal
	  var modal = document.getElementById('forgotPasswordDiv');

	  // When the user clicks anywhere outside of the modal, close it
	  window.onclick = function(event) {
		  if (event.target == modal) {
			  modal.style.display = "none";
		  }
	  }
  }

  onLogin(event) {
	  this.errorMessage = "";
	  this.loginService.login(this.login)
		  .subscribe(login => {
			  if (login.username == this.login.username && login.password == this.login.password) {
				  this.login = login;
				  // save token 
				  //this.tokenService.saveToken(login.accessToken);
				  this.router.navigate(['/onboard']);
			  } else {
				  this.errorMessage = "Invalid username and password.";
			  }
		  	},
		  	error => { this.errorMessage = error; });
	  
  }

}



