import {Component, ElementRef, EventEmitter, Injectable, Inject, AfterViewInit, ChangeDetectorRef, Input, Output} from '@angular/core';
declare var jQuery:any;

@Component({
    selector: 'slider',
    template: 
    `
    <div id="sliderDir"></div>
    <h5 style='text-align: center;'>Value = {{slideValue.value}}%</h5>
    `
})

export class SliderDirective implements AfterViewInit {
    elementRef: ElementRef;
    @Input() slideValue;

    constructor(@Inject(ElementRef) elementRef: ElementRef, public cdr: ChangeDetectorRef) {
      this.elementRef = elementRef;
    }

    ngAfterViewInit() { 
      jQuery(this.elementRef.nativeElement).find("#sliderDir").slider({
        range: false,
        orientation: "horizontal",
        min: 0,
        max: 100,
        value: this.slideValue.value,
        slide: ( event, ui ) => {
          this.slideValue.value = ui.value;
        }
      });
    }
}


