import { Routes } from '@angular/router';

import { OnBoardComponent } from './onboard.component';
import { OnBoardHomeComponent } from './onboard.home.component';
import { OnBoardListComponent } from './onboard.list.component';
import { AuthGuard } from '../guards/auth.guard';

export const routesOnboard: Routes = [
	{
		path: 'onboard', component: OnBoardComponent, //, canActivate: [AuthGuard]
		children: [
			{ path: '', component: OnBoardHomeComponent },
			{ path: 'list/:id', component: OnBoardListComponent },
		]
	}
];

//export const onboardRouting = RouterModule.forChild(routes);


