import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpModule } from "@angular/http";
import { FormsModule } from '@angular/forms';
import { Router, RouterModule } from '@angular/router';
import { CookieService } from 'angular2-cookie/services/cookies.service';

import { PageNotFoundComponent } from './pagenotfound.component';
import 'rxjs/Rx';

/* App Root */
import { AppComponent } from './app.component';

import { HomeComponent } from './home/home.component';
/* Feature Modules */
import { OnBoardModule } from './onboard/onboard.module';

/* Routing */
import { appRoutes } from './app.routing';
import { LoginComponent } from './login/login.component';

//Shared Services
import { TokenService } from './services/token/token.service';
import { LoginService } from './login/login.service';
import { HttpHelperService } from './services/httphelper/httphelper.service';
import { AuthGuard } from './guards/auth.guard';
import { OnBoardService } from './onboard/onboard.service';

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    PageNotFoundComponent,
    LoginComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    OnBoardModule,
    RouterModule.forRoot(appRoutes)
  ],
  exports: [
  ],
  providers: [AuthGuard, HttpHelperService, TokenService, LoginService, CookieService, OnBoardService],
  bootstrap: [AppComponent]
})
export class AppModule { 
  constructor(private router: Router, private onBoardService: OnBoardService) {
    onBoardService.dashboard()
      .subscribe(login => {
      this.router.navigate(['/onboard']);
      },
    error => { this.router.navigate(['/login']); });
  }
}



